import { Component, Input, Output, EventEmitter } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Router, ActivatedRoute } from '@angular/router';
import { DialogState } from '../../models';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styles: []
})
export class LoginFormComponent {

  @Input() redirect: string|boolean;
  @Output() login = new EventEmitter<boolean>();
  @Output() action = new EventEmitter<DialogState>();

  credentials = {
    email: '',
    password: ''
  };

  DialogState = DialogState;

  constructor(private authService: AuthService, private router: Router, private route: ActivatedRoute) { }

  async onSubmit() {
    try {
      const res = await this.authService.login(this.credentials).toPromise();

      const returnUrl = this.route.snapshot.queryParams['returnUrl'];
        if (returnUrl) {
          this.router.navigateByUrl(returnUrl);
        } else if (this.redirect !== false && res) {
        this.router.navigate([this.redirect ? this.redirect : res.user.userName]);
      }
      this.login.emit(true);
    } catch (error) {
      console.log(error);
    }
  }

}
