import { Input, Directive } from '@angular/core';
import { ValidatorFn, Validator, AbstractControl, NG_VALIDATORS } from '@angular/forms';

@Directive({
  selector: '[appForbiddenValues]',
  providers: [{provide: NG_VALIDATORS, useExisting: ForbiddenValidatorDirective, multi: true}]
})
export class ForbiddenValidatorDirective implements Validator {
  @Input('appForbiddenValues') forbiddenValues: string[];

  validate(control: AbstractControl): {[key: string]: any} | null {

    return this.forbiddenValues ? forbiddenValuesValidator(this.forbiddenValues)(control)
                              : null;
  }
}

export function forbiddenValuesValidator(values: string[]): ValidatorFn {
  return (control: AbstractControl): {[key: string]: any} | null => {
    const forbidden = values.includes(control.value);
    return forbidden ? {'forbiddenValues': {value: control.value}} : null;
  };
}
