import { Component, OnInit, Input, Output, EventEmitter, ViewChild, SimpleChanges, OnChanges } from '@angular/core';
import { CollectionView, SortDescription, PropertyGroupDescription, DataType } from 'wijmo/wijmo';
import { PopoverDirective } from 'ngx-bootstrap/popover';
import { FlexGrid, Row, CellType } from 'wijmo/wijmo.grid';
import { map } from 'rxjs/operators';
import { ColumnInfo } from 'src/app/wijmo/models';
import { FilterItem, MetaDataObject, FilterSqlData, ColumnFilterData } from '../../models';
import { SqlDbService } from '../../services';
import { getFilters, dateToDtoString, formatWijmoDate } from '../../utils';


@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
  styleUrls: ['./filter.component.scss']
})
export class FilterComponent implements OnInit, OnChanges {

  @Input() icon: boolean;
  @Input() field: ColumnInfo;
  // ToDo: It has to be strongly typed filter array
  @Input() currentFilters: {[key: string]: Array<string|Date>};
  @Input() dataObject: MetaDataObject;
  @Input() connection: string;
  @Input() defaultFilters: FilterSqlData[];
  @Output() delete = new EventEmitter();
  @Output() apply = new EventEmitter<Array<string>|Array<Date>>();
  @ViewChild('grid', { static: false }) grid: FlexGrid;
  @ViewChild('pop', { static: false }) pop: PopoverDirective;

  collectionView = new CollectionView();
  search: string;
  selected: Array<string>|Array<Date>;
  label: string;
  title: string;
  list: FilterItem[];
  loading = true;
  usedFilters: string;
  constructor(private sql: SqlDbService) { }

  ngOnInit() {
    this.collectionView.sourceCollection = [];
    this.collectionView.filter = ((item: FilterItem) => {
      return !this.search || item.value.toLowerCase().indexOf(this.search.trim().toLowerCase()) !== -1;
    });
    this.setSelected(this.currentFilters);
    const groupDesc = new PropertyGroupDescription('applied', (item, prop) => {
      return this.isSelected(item.value) ? 'Applied items' : 'Select items';
    });

    this.collectionView.groupDescriptions.push(groupDesc);

    this.collectionView.sortConverter = (sd, item, value) => {
      if (sd.property === 'applied' && this.selected) {
        return this.isSelected(item.value);
      }
      return value;
    };

    this.setLabel();
  }

  /** @description Handles filter popup show.*/
  async onShown(): Promise<void> {
    this.loading = true;
    try {
      const items = await this.getItems();
      this.collectionView.sourceCollection = items;
      this.setFooter(this.grid);
    } catch (e) {
      console.error(e);
    }
    finally {
      this.loading = false;
    }
  }

  private getItems(): Promise<ColumnFilterData[]> {
    let filters = getFilters(this.currentFilters, this.field.bindingName) || [];
    if (this.defaultFilters) {
      filters = [...filters, ...this.defaultFilters];
    }
    return this.sql.selectSqlObjectData(this.connection, this.dataObject.objectOwner, this.dataObject.objectName, {
      fields: [
        this.field.bindingName,
        `Count|1`
      ],
      filters
    }).pipe(map(res => {
      this.usedFilters = this.getUsedFiltersStr();
      return res.rows && res.rows.map(([value, count]) => {
        if (this.field.dataType === DataType.Date) {
          value = new Date(Date.parse(value));
        }
        return { selected: this.isSelected(value), value, count };
      });
    })).toPromise();
  }

  private setSelected(currentFilters: Object) {
    if (currentFilters) {
      const selected = currentFilters[this.field.bindingName];
      this.selected = selected;
    }
  }

  ngOnChanges({ currentFilters }: SimpleChanges) {
    if (currentFilters && !currentFilters.firstChange) {
      this.setSelected(currentFilters.currentValue);
      this.setLabel();
    }
  }

  sortingHandler(s: FlexGrid, e) {
    const col = s.columns[e.col];
    const asc = col.currentSort !== '+';
    const sorts = this.collectionView.sortDescriptions;
    const sd = new SortDescription(col.binding, asc);
    this.collectionView.deferUpdate(() => {
      sorts.clear();
      sorts.push(new SortDescription('applied', false));
      sorts.push(sd);
    });
    e.cancel = true;
  }

  onGridInit(grid: FlexGrid) {
    grid.columnFooters.rows.push(new Row({ count: 0 }));
    grid.groupHeaderFormat = `<b>{value}</b>`;

    grid.formatItem.addHandler((g, r: any) => {
      const panel = r.panel;
      const dataItem = panel.rows[r.row].dataItem;
      const binding = panel.columns[r.col].binding;
      if (panel.cellType === CellType.Cell && dataItem && binding === 'value') {
        if (dataItem[binding] === null) {
          r.cell.innerHTML = '[NULL]';
        }
      }
    });

    if (this.selected) {
      this.collectionView.sortDescriptions.push(new SortDescription('applied', false));
    }
  }

  onSearch(str: string) {
    this.search = str;
    this.collectionView.refresh();
  }

  onHidden() {
    if (this.currentFilters) {
      this.collectionView.sourceCollection = [];
    }
  }

  onApply() {
    this.selected = this.collectionView.items
      .filter(({ selected }) => selected)
      .map(({ value }) => value);
    if (!this.selected.length) {
      this.selected = null;
    }
    this.pop.hide();
    this.apply.emit(this.selected);
    this.setLabel();
  }

  onClear() {
    this.selected = null;
    this.onHidden();
    this.pop.hide();
    this.apply.emit(this.selected);
    this.setLabel();
  }

  onDelete() {
    this.selected = null;
    this.delete.emit();
    this.setLabel();
  }

  /** @description Set footer with total count. */
  private setFooter(grid: FlexGrid): void {
    if (grid) {
      grid.columnFooters.setCellData(0, 2, this.collectionView.sourceCollection.reduce((prev, item) => {
        prev += item.count;
        return prev;
      }, 0));
    }
  }

  /** @description Set label and title */
  private setLabel(): void {
    let label = this.field.bindingName;
    let title = label;
    if (this.selected && this.selected.length) {
      title = `${label}: ${(this.selected as []).map(v => {
        if (this.field.dataType === DataType.Date) {
          return formatWijmoDate(v, this.field.displayFormat);
        }
        return v;
      }).join(', ')}`;
      if (title.length > 50) {
        label = `${title.substring(0, 47)}...`;
      } else {
        label = title;
      }
    }
    this.label = label;
    this.title = title;
  }

  /** @description Is value selected */
  private isSelected(value: string|Date): boolean {
    if (!this.selected) {
      return false;
    }
    if (value instanceof Date) {
      return this.selected.some((s) => s.getTime() === value.getTime());
    }
    return (this.selected as string[]).includes(value);
  }

  private getUsedFiltersStr(): string {
    const filters = getFilters(this.currentFilters, this.field.bindingName);
    return filters &&
        filters.map(f => `${f.fieldName} IN (${f.value.map(v => v instanceof Date ? dateToDtoString(v) : v ).join(',')}})`).join(' AND ');
  }
}
