import { Injectable } from '@angular/core';
import { registerAction, ExecutionContext, JSONUtils, ActionResult, ActionStatus } from '@ngx-dynamic-components/core';
import { SqlDbService } from './sql-db.service';
import { NotificationActionConfig, AuthorizeConfig, EnsureConfig, TableToArrayConfig } from '../models';
import { NotificationService } from './notification.service';
import { AuthService } from 'src/app/security';
import { switchMap, map, catchError } from 'rxjs/operators';
import { generateFields, verifyFields, mapTableToValues } from '../utils';
import { of } from 'rxjs';
import { Variables } from '../app-variables';

@Injectable({
  providedIn: 'root'
})
export class ActionsService {
  constructor(
    private notificationService: NotificationService,
    private authService: AuthService,
    private sqlDb: SqlDbService) {

    function notificationAction(context: ExecutionContext, {type, message}: NotificationActionConfig) {
      message = Variables.resolveString(message, context.variables);
      notificationService.open({type, message});
      return { message };
    }

    registerAction('notification', {
      name: 'notification',
      method: notificationAction,
      category: 'Common',
      config: {
        type: 'success',
        message: 'Error message'
      } as NotificationActionConfig,
      description: 'Shows notification',
    });

    async function authorizeAction(context: ExecutionContext, config: AuthorizeConfig) {
      return await authService.authorize(config.publicAccessCode, config.saveAs).toPromise();
    }

    registerAction('ws-login', {
      name: 'ws-login',
      method: authorizeAction,
      category: 'Auth',
      config: {
        publicAccessCode: 'TEST_TOKEN',
        saveAs: 'bearer_token'
      } as AuthorizeConfig,
      description: 'Authorize user role by public token'
    });

    async function ensureTable(context: ExecutionContext, config: EnsureConfig): Promise<ActionResult> {
      const {tableName, ownerName, sqlConnectionUrl} = config;
      let fields = [];
        return await sqlDb.getObject(sqlConnectionUrl, ownerName, tableName).pipe(
          switchMap(tableData => {
            const objectId = tableData ? tableData.objectId : 0;
            const uiModel = context.variables.get('uiModel');
            let fieldsProperties = JSONUtils.find(uiModel, '$(children)/itemProperties');
            if (!Array.isArray(fieldsProperties)) {
              fieldsProperties = [fieldsProperties];
            }
            const dataModelPaths = fieldsProperties.filter(f => f.dataModelPath).map(f => f.dataModelPath.replace('$.', ''));
            fields = generateFields(dataModelPaths);

            if (tableData && verifyFields(tableData.fields, fields)) {
              return of({
                result: tableData,
                status: ActionStatus.SUCCESS
              });
            }

            const dataObject = {
              fields,
              objectId,
              objectOwner: ownerName,
              objectName: tableName,
              objectType: 'Table',
              folder: 'forms-folder'
            };
            return sqlDb.generateObject(sqlConnectionUrl, dataObject).pipe(
              map(res => ({
                result: res,
                status: ActionStatus.SUCCESS
              })),
              catchError(e => of({
                result: e,
                status: ActionStatus.FAILED
              })));
          })).toPromise();
    }

    registerAction('ensureTableWithFields', {
      name: 'ensureTableWithFields',
      method: ensureTable,
      category: 'DesignTime',
      config: {
        actionType: 'ensureTableWithFields',
        sqlConnectionUrl: '{{sql_api_url}}',
        ownerName: '{{ownerName}}',
        tableName: '{{tableName}}',
        generateFields: 'fromDataModel'
      } as EnsureConfig,
      description: 'Ensures table structure fits data model'
    });

    const tableDtoToArray = (context: ExecutionContext, config: TableToArrayConfig) => {
      try {
        let rows = Variables.resolveString(config.rows, context.variables);
        if (!Array.isArray(rows)) {
          rows = [];
        }
        console.log('Resolved rows', rows);

        let fieldNames = Variables.resolveString(config.fieldNames, context.variables);
        if (!Array.isArray(fieldNames)) {
          fieldNames = [];
        }
        console.log('Resolved fieldNames', fieldNames);
        let dataTypes = null;

        if (config.dataTypes) {
          dataTypes = Variables.resolveString(config.dataTypes, context.variables);
          if (!Array.isArray(dataTypes)) {
            dataTypes = [];
          }
        }

        const res = mapTableToValues({ rows, fieldNames, dataTypes });
        console.log('Action tableDtoToArray result', res);
        return res;
      } catch (e) {
        console.error(e);
        return null;
      }
    };

    /**
     * Action converts Data Transfer Object to Array.
     * @param ExecutionContext context
     * @param TableToArrayConfig config
     * @example
     * {
      *  "actionName": "step1"
      *  "actionType": "tableDtoToArray",
      *  "fieldNames": "{{responseContent}}/dataset/column_names",
      *  "rows": "{{responseContent}}/dataset/data"
      * }
      */
     registerAction('tableDtoToArray', {
       name: 'tableDtoToArray',
       category: 'Rest Client',
       method: tableDtoToArray,
       config: {
         actionName: 'table-dto',
         actionType: 'tableDtoToArray',
         fieldNames: '{{responseContent}}/dataset/column_names',
         rows: '{{responseContent}}/dataset/data',
         dataTypes: '{{responseContent}}/dataset/dataTypes'
       },
       description: 'Formats table data to array',
       getMessage() {
         return `Formatted table data`;
       }
     });
  }
}
