import { Component, OnInit, OnDestroy, Input, Output, SimpleChanges, OnChanges, EventEmitter } from '@angular/core';
import { map, switchMap } from 'rxjs/operators';
import { Subject, BehaviorSubject, Observable } from 'rxjs';
import { MetaDataObject, FilterSqlData, ColumnFilter } from 'src/app/shared/models';
import { DialogService } from 'src/app/shared/services';
import { ManageFiltersComponent } from 'src/app/shared/components';
import { ColumnInfo } from 'src/app/wijmo/models';

/** @description Filters for Table data. */
@Component({
  selector: 'app-filters',
  templateUrl: './filters.component.html',
  styleUrls: ['./filters.component.scss']
})
export class FiltersComponent implements OnInit, OnDestroy, OnChanges {

  @Input() fields: ColumnInfo[];
  @Input() currentFilters: any;
  @Input() connection: string;
  @Input() dataObject: MetaDataObject;
  @Input() defaultFilters: FilterSqlData[];
  @Output() changed = new EventEmitter();
  fields$: Observable<ColumnInfo[]>;
  destroy$ = new Subject();
  filters$ = new BehaviorSubject<ColumnFilter[]>([]);
  columnInfo$: Observable<ColumnInfo[]>;
  selectedFilters$: Observable<ColumnFilter[]>;
  value: string;
  filtersState: FilterSqlData[] = null;

  constructor(private dialogService: DialogService) { }

  ngOnInit() {
    this.fields$ = this.filters$.pipe(
      map(filters => {
        const selected = filters.map(f => f.bindingName);
        return this.fields.filter(f => !selected.includes(f.bindingName));
      }));

    this.columnInfo$ = this.filters$.pipe(
      map(filters => filters.map(f => this.fields.find(({bindingName}) => f.bindingName === bindingName))));

    this.selectedFilters$ = this.filters$.pipe(
      map(f => f.filter(i => i.selected && i.selected.length)));
  }

  ngOnChanges({fields}: SimpleChanges) {
    if (fields && !fields.firstChange) {
      this.filters$.next(this.getSavedFilters());
    }
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  addFilter(field: ColumnInfo): void {
    const filter = {...field, selected: null, items: []} as ColumnFilter;
    this.filters$.next([...this.filters$.value, filter]);
    this.saveFilters();
  }

  /**
   * Reloads all filters that goes after current.
   * @param index Index of changed filter.
   * @param selected New filter value.
   */
  async onFilterChange(index: number, selected: string[], field: ColumnInfo): Promise<void> {
    this.currentFilters[field.bindingName] = selected;
    this.changed.emit(this.currentFilters);
  }

  async removeFilter(index: number, field: ColumnInfo): Promise<void> {
    await this.onFilterChange(index, null, field);
    this.currentFilters[field.bindingName] = null;
    this.filters$.value.splice(index, 1);
    this.filters$.next([...this.filters$.value]);
    this.saveFilters();
  }

  /** @description Shows dialog for managing filters. */
  manageFilter() {
    const manageFiltersDialog = this.dialogService.open(ManageFiltersComponent);
    manageFiltersDialog.config = {
      filters: this.filters$.value,
      fields: this.fields
    };
    manageFiltersDialog.init.pipe(
      switchMap(() => manageFiltersDialog.component.change)).subscribe(async res => {
        const filters = res as ColumnFilter[];
        this.filters$.value.filter(f => !filters.map(({bindingName}) => bindingName).includes(f.bindingName)).forEach(({bindingName}) => {
          this.currentFilters[bindingName] = null;
        });
        const index = this.filters$.value.findIndex((f, i) => !filters[i] || f.bindingName !== filters[i].bindingName);
        if (index !== -1 || filters.length !== this.filters$.value.length) {
          this.changed.emit(this.currentFilters);
        }

        this.filters$.next(filters);
        this.saveFilters();
    });
  }

  private get filtersKey() {
    return `db-table-filters-${this.dataObject.objectOwner}-${this.dataObject.objectName}`;
  }

  private saveFilters() {
    const filters = this.filters$.value.map(f => f.bindingName);
    if (filters.length) {
      localStorage.setItem(this.filtersKey, JSON.stringify(filters));
    } else {
      localStorage.removeItem(this.filtersKey);
    }
  }

  private getSavedFilters(): ColumnFilter[] {
    try {
      const filters = localStorage.getItem(this.filtersKey);
      if (filters) {
        const bindings = JSON.parse(filters) as string[];
        return bindings.map(binding => {
          const field = this.fields.find(f => f.bindingName === binding);
          if (field) {
            return {...field, selected: null, items: []} as ColumnFilter;
          }
        }).filter(f => f);
      }
      return [];
    } catch (e) {
      return [];
    }
  }
}
