import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AuthService } from 'src/app/security/services';
import { MetaDataObject, SqlObject, DBConnection, SqlTableData, TableDto, SqlSaveStatus, SelectBody } from '../models';
import { map, catchError, switchMap } from 'rxjs/operators';
import { Observable, throwError, of, Subject, BehaviorSubject } from 'rxjs';
import { environment } from 'src/environments/environment';
import { getErrorMessage, dateToDtoString } from '../utils';
import { ObjectType } from 'src/app/database-manager/models';

const API_URL = `${environment.apiBaseUrl}/sql`;

@Injectable({
  providedIn: 'root'
})
export class SqlDbService {

  constructor(private http: HttpClient, private authService: AuthService) { }

  getMyDbConnections(): Observable<DBConnection[]> {
    const url = `${environment.apiBaseUrl}/sql/my-db-connections`;
    return this.http.get<DBConnection[]>(url, this.authService.getHttpHeaders());
  }

  getObject(sqlConnectionUrl: string, objectOwner: string, objectName: string, objectType = ObjectType.Table): Observable<MetaDataObject> {
    let url = `${sqlConnectionUrl}/db-object/${objectOwner}/${objectName}/Fields,PrimaryKeys`;
    if (objectType === ObjectType.View) {
      url += ',SqlText';
    }
    return this.http.get<MetaDataObject>(url, this.authService.getHttpHeaders()).pipe(
      catchError(e => {
        return throwError(e);
      })
    );
  }

  generateObject(sqlConnectionUrl: string, dataObject: MetaDataObject): Observable<MetaDataObject> {
    const { objectOwner, objectName, objectId } = dataObject;
    const url = `${sqlConnectionUrl}/db-object/${objectOwner}/${objectName}`;

    if (objectId !== 0) {
      return this.http.post<number>(url, dataObject, this.authService.getHttpHeaders()).pipe(
        map(id => {
          dataObject.objectId = id;
          return dataObject;
        }));
    }

    return this.getSqlObjects(sqlConnectionUrl).pipe(
      switchMap((objects) => {
        if (objects.map(o => `${o.objectOwner}${o.objectName}`).includes(`${objectOwner.toLowerCase()}${objectName}`)) {
          return throwError('Table name already exist.');
        } else {
          return this.http.post<number>(url, dataObject, this.authService.getHttpHeaders()).pipe(
            map(id => {
              dataObject.objectId = id;
              return dataObject;
            }));
        }
      }));
  }

  deleteObject(sqlConnectionUrl: string, object: MetaDataObject): Observable<number> {
    const { objectOwner, objectName } = object;
    const url = `${sqlConnectionUrl}/db-object/${objectOwner}/${objectName}`;
    return this.http.delete<number>(url, this.authService.getHttpHeaders());
  }

  getSqlObjects(sqlConnectionUrl: string): Observable<SqlObject[]> {
    return this.http.get<SqlObject[]>(`${sqlConnectionUrl}/db-objects`, this.authService.getHttpHeaders()).pipe(
      catchError(e => throwError(e))
    );
  }

  /** @deprecated */
  getSqlObjectData(sqlConnectionUrl: string, objectOwner: string, objectName: string) {
    const url = `${sqlConnectionUrl}/select-all/from/${objectOwner}/${objectName}`;
    return this.http.get<SqlTableData>(url, this.authService.getHttpHeaders()).pipe(
      map(res => res.table),
      catchError(e => throwError(typeof e.error === 'string' ? e.error : 'Not found')));
  }

  selectSqlObjectData(connection: string, objectOwner: string, objectName: string, selectBody: SelectBody): Observable<TableDto> {
    const url = `${API_URL}/${connection}/select/from/${objectOwner}/${objectName}`;
    const correctValueFn = (filterValue: any): any => filterValue instanceof Date
      ? dateToDtoString(filterValue) : filterValue;

    // clone filters and convert dates
    selectBody = Object.assign({}, selectBody);
    if (selectBody.filters) {
      const filters = [];
      for (const filter of selectBody.filters) {
        const newFilter = Object.assign({}, filter);
        newFilter.value = Array.isArray(filter.value) ?
          filter.value.map(v => correctValueFn(v)) : correctValueFn(filter.value);
        filters.push(newFilter);
      }

      selectBody.filters = filters;
    }
    return this.http.post<SqlTableData>(url, selectBody, this.authService.getHttpHeaders()).pipe(
      map(res => res.table),
      catchError(e => throwError(getErrorMessage(e, 'Not found'))));
  }

  saveSqlObjectDataWithStatus(sqlConnectionUrl: string, dataObject: SqlObject, data: any): Observable<SqlSaveStatus> {
    const url = `${sqlConnectionUrl}/save/${dataObject.objectOwner}/${dataObject.objectName}`;
    const s = new BehaviorSubject<SqlSaveStatus>({
      inserted: 0, deleted: 0, updated: 0, savedItemsCount: 0, itemsToSaveCount: data.tableData.rows.length
    });
    this.saveSqlObjectBatchData(url, data, s).subscribe(() => s.complete(), (e) => s.error(e));
    return s;
  }

  saveSqlObjectData(connection: string, dataObject: SqlObject, data: any): Observable<SqlSaveStatus> {
    const url = `${API_URL}/${connection}/save/${dataObject.objectOwner}/${dataObject.objectName}`;
    return this.saveSqlObjectBatchData(url, data, null);
  }

  private saveSqlObjectBatchData(url: string, data: { tableData: TableDto }, s: Subject<SqlSaveStatus>, index = 0, res: SqlSaveStatus = {
    inserted: 0, updated: 0, deleted: 0, itemsToSaveCount: 0, savedItemsCount: 0
  }): Observable<SqlSaveStatus> {
    const rows = [];
    const allRows = data.tableData.rows;
    let currentIndex = index;
    let currentSize = 0;

    const maxTableSize = 1500000;
    const maxRowsCount = 10000 + index;
    while (currentIndex < maxRowsCount && currentSize < maxTableSize && currentIndex < allRows.length) {
      rows.push(allRows[currentIndex]);
      currentSize += JSON.stringify(allRows[currentIndex]).length;
      currentIndex++;
    }

    return this.http.post<SqlSaveStatus>(url, { tableData: { ...data.tableData, rows } }, this.authService.getHttpHeaders()).pipe(
      switchMap((response) => {
        res.inserted += response.inserted;
        res.updated += response.updated;
        res.deleted += response.deleted;
        res.savedItemsCount = currentIndex;
        res.itemsToSaveCount = allRows.length;
        if (s) {
          s.next(res);
        }
        if (currentIndex < allRows.length) {
          return this.saveSqlObjectBatchData(url, data, s, currentIndex, res);
        } else {
          return of(res);
        }
      }));
  }
}
