import { Injectable } from '@angular/core';

import { formatObjToJsonStr, formatToPrimitive } from '../utils';
import { Observable, Subscriber } from 'rxjs';
import { ExportConfig, FileType } from '../models';
import { parse, unparse } from 'papaparse';
import { saveAs } from 'file-saver';

@Injectable({
  providedIn: 'root'
})
export class FileManagerService {

  private reader: FileReader;
  private anchor: HTMLAnchorElement;
  private input: HTMLInputElement;
  private defaultExportConfig: ExportConfig = {
    fileType: FileType.JSON,
    fileName: 'rest-client'
  };

  constructor() {
    this.reader = new FileReader();
    this.input = document.createElement('input');
    this.input.type = 'file';
    this.anchor = document.createElement('a');
  }

  importFromFile(type: FileType = FileType.JSON): Observable<any> {
    const input = document.createElement('input');
    input.type = 'file';
    return new Observable((observer: Subscriber<any>) => {
      input.onchange = (evt: any) => {
        evt.stopPropagation();
        evt.preventDefault();
        const files = evt.target.files;
        if (files.length) {
          const file = files[0];

          this.reader.onloadend = async (e: any) => {
            if (e.target.readyState === FileReader.DONE) {
              try {
                const result = this.parseData(type, e.target.result);
                observer.next({result, file});
                observer.complete();
              } catch (e) {
                observer.error(e);
              }
            }
          };
          this.reader.readAsBinaryString(file);
        }
      };
      input.click();
    });
  }

  importCSVFile(): Observable<any> {
    const input = document.createElement('input');
    input.type = 'file';
    return new Observable((observer: Subscriber<any>) => {
      input.onchange = (evt: any) => {
        evt.stopPropagation();
        evt.preventDefault();
        const files = evt.target.files;
        if (files.length) {
          const file = files[0];
          observer.next({file});
          parse(file, {
            dynamicTyping: true,
            skipEmptyLines: true,
            encoding: 'ISO-8859-1',
            complete(result) {
              observer.next({result, file});
              observer.complete();
            },
            error(e) {
              observer.error(e);
            }
          });
        }
      };
      input.click();
    });
  }

  exportToFile(sendData: object, config?: ExportConfig) {
    config = {...this.defaultExportConfig, ...config };
    const data = this.formatData(sendData, config.fileType);
    const { fileName, fileType } = config;
    const fileExtension = fileType.split('/')[1];
    const blob = new Blob([data], { type: fileType });
    saveAs(blob, `${fileName}.${fileExtension}`);
  }

  private parseData(type: FileType, value: string) {
    switch (type) {
      case FileType.JSON: return JSON.parse(value);
      case FileType.CSV: return parse(value, {
        worker: true,
        dynamicTyping: true
      });
      default: return null;
    }
  }

  private formatData(data, type: FileType): string {
    switch (type) {
      case FileType.JSON: return encodeURIComponent(formatObjToJsonStr(data));
      case FileType.CSV: {
        if (Array.isArray(data)) {
          return unparse(formatToPrimitive(data) as []);
        }
      }
      // @deprecated
      // case FileType.CSV: {
      //   if (Array.isArray(data) && data.length) {
      //     const header = Object.keys(data[0]).join(',') + '\r\n';
      //     return header + data.map((row) => {
      //       return Object.values(row).map(v => {
      //         if (typeof v === 'object') {
      //           return JSON.stringify(v);
      //         }
      //         return v;
      //       }).join(',');
      //     }).join('\r\n');
      //   }
      //   return '';
      // }
    }
  }
}
