import { Component, ViewChild } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { DialogState } from '../../models';

@Component({
  selector: 'app-login-dialog',
  template: `
  <div bsModal #modal="bs-modal" class="modal fade" tabindex="-1" role="dialog" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content shadow" [ngSwitch]="state">
        <app-login-form *ngSwitchCase="DialogState.LOGIN"
          [redirect]="false" (login)="onLogin($event)" (action)="state = $event"></app-login-form>
        <app-register-form (register)="onRegister()" *ngSwitchCase="DialogState.REGISTRATION"
          (login)="state = DialogState.LOGIN"></app-register-form>
        <app-reset-password-request-form *ngSwitchCase="DialogState.RESET_REQUEST"
          (action)="state = $event" (requested)="onLogin(true)"></app-reset-password-request-form>
      </div>
    </div>
  </div>
  `
})
export class LoginDialogComponent {

  @ViewChild('modal', {static: true}) public modal: ModalDirective;

  state = DialogState.LOGIN;
  DialogState = DialogState;

  constructor() { }

  openModal() {
    this.modal.show();
  }

  onLogin(evt: boolean) {
    if (evt) {
      this.modal.hide();
    }
  }

  onRegister() {
    this.state = DialogState.LOGIN;
  }
}
