import { UIModel } from '@ngx-dynamic-components/core';

export interface MenuItem {
  icon: string;
  label: string;
  separator?: boolean;
  tag?: any;
  command?: (MenuItem?: any) => void | Promise<any>;
  items?: MenuItem[];
  title?: string;
}

export enum NotificationType {
  SUCCESS = 'success',
  INFO = 'info',
  WARNING = 'warning',
  DANGER = 'danger'
}

export interface NotificationConfig {
  type: NotificationType;
  message: string;
  backdrop?: boolean;
}

export interface PageShortInfo {
  pageId?: number;
  pageName: string;
  title: string;
  description: string;
}

export interface PageInfo extends PageShortInfo {
  uiModel: UIModel;
  workflows?: any;
  showInDialog?: boolean;
  icon?: string;
  workflowsMap?: any;
}

export interface LayoutConfig {
  icon: string;
  smIcon: string;
  appName: string;
  collapsed?: boolean;
  logoutRedirect?: boolean;
}

export interface LocalSettings {
  requests: object;
  sidebar: object;
}

export enum FileType {
  JSON = 'text/json',
  CSV = 'text/csv'
}

export interface ExportConfig {
  fileType?: FileType;
  fileName?: string;
}

export interface FilterItem {
  selected?: boolean;
  value: string;
  count: number;
}
