import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { DragulaService } from 'ng2-dragula';
import { ColumnInfo } from 'src/app/wijmo/models';
import { ColumnFilter } from '../../models';

@Component({
  selector: 'app-manage-filters',
  templateUrl: './manage-filters.component.html',
  styleUrls: ['./manage-filters.component.scss']
})
export class ManageFiltersComponent implements OnInit, OnDestroy {

  @Input() modalRef: ModalDirective;
  @Input() config: {filters: ColumnFilter[], fields: ColumnInfo[]};
  @Output() change = new EventEmitter<ColumnFilter[]>();
  filters: ColumnFilter[];
  fields: ColumnInfo[];

  constructor(private dragulaService: DragulaService) {
    dragulaService.createGroup('filters', {
      moves: (el, container, handle) => {
        return handle.classList.contains('handle');
      }
    });
  }

  ngOnInit() {
    this.filters = [...this.config.filters];
    this.setFields();
  }

  ngOnDestroy() {
    this.dragulaService.destroy('filters');
  }

  onApply() {
    this.change.emit(this.filters);
    this.modalRef.hide();
  }

  addFilter(field: ColumnInfo) {
    this.filters.push({...field, selected: null, items: []} as ColumnFilter);
    this.setFields();
  }

  removeFilter(index: number) {
    this.filters.splice(index, 1);
  }

  private setFields() {
    const selected = this.filters.map(f => f.bindingName);
    this.fields = this.config.fields.filter(f => {
      return !selected.includes(f.bindingName);
    });
  }

}
