import { NameValue } from '../rest-client/models';
import { IVariableResolver, JSONUtils, mapToObj, resolveWorkflowPath } from '@ngx-dynamic-components/core';

/**
 * AppVariables implements container for global and context variables and variable resolver.
 */
class AppVariables implements IVariableResolver {
  private readonly STORAGE_KEY = 'app-variables';
  /** LocalStorage variables */
  private _variables: any;
  /** Application context variables */
  private _contextVariables: {[key: string]: any} = {};

  constructor() {
    try {
      this._variables = JSON.parse(localStorage.getItem(this.STORAGE_KEY)) || {};
    } catch (e) {
      this._variables = {};
      console.error(e);
    }
  }

  get variables() {
    return this._variables;
  }

  get contextVariables() {
    return this._contextVariables;
  }

  get currentEnv() {
    return localStorage.getItem('env');
  }

  set currentEnv(val) {
    if (val) {
      localStorage.setItem('env', val);
    } else {
      localStorage.removeItem('env');
    }
  }

  setVariable(key: string, value: any) {
    this._variables[key] = value;
    this.syncAppVariables();
  }

  syncAppVariables() {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.variables));
  }

  setVariables(vars: {name: string, value: any}[]) {
    this._variables = vars.filter(v => v.name).reduce((variables, v) =>  {
      variables[v.name] = v.value;
      return variables;
    }, {});
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.variables));
  }

  setContextVariable({name, value}: NameValue) {
    this._contextVariables[name] = value;
  }

  removeContextVariable(name: string) {
    delete this._contextVariables[name];
  }

  getContextVar(key: string) {
    return this.contextVariables[key];
  }

  addVariables(vars: {name: string, value: any}[]) {
    this._variables = vars.filter(v => v.name).reduce((variables, v) =>  {
      variables[v.name] = v.value;
      return variables;
    }, this._variables);
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(this.variables));
  }

  /**
   * Resolves object property values.
   * @param obj Object to resolve property values.
   */
  resolve<T>(obj: object|Array<any>|string, vars?): T {
    if (Array.isArray(obj)) {
      return obj.map(o => (this.resolve(o, vars))) as any;
    }
    if (typeof obj === 'string') {
      return this.resolveString(obj, vars);
    }
    const resolved = Object.keys(obj).reduce((prev, key) => {
      const val = obj[key];
      if (typeof val === 'string') {
        prev[key] = this.resolveString(val, vars);
      } else if (Array.isArray(val)) {
        prev[key] = val.map(o => this.resolve(o, vars));
      } else if (typeof val === 'object') {
        prev[key] = this.resolve(obj[key], vars);
      } else {
        prev[key] = val;
      }

      return prev;
    }, {});
    return resolved as T;
  }

  /**
   * Resolves variables in string.
   * @param str String to resolve.
   * @param vars Object containing variables.
   * @example
   * resolveString('{{objectVariable}}/subProp/subProp2') => 'subProp2Value'; (value of subProp2);
   * resolveString('{{stringVariable}}/subProp/subProp2') => 'stringVariableValue/subProp/subProp2'; (resolved string);
   */
  resolveString(str: string, vars?: object|Map<string, any>) {
    if (typeof str !== 'string') {
      return str;
    }
    vars = vars || {};
    if (vars instanceof Map) {
      vars = mapToObj(vars);
    }
    const pathConstructions = str.match(/{{[\w-]*}}+[\w\/]*/g);

    if (pathConstructions) {
      let result: any = str;
      pathConstructions.forEach(v => {
        const [variable, path] = v.split('}}/');
        const key = variable.replace('{{', '').replace('}}', '');
        const val = vars[key] || this.variables[key] || this.contextVariables[key]; // || v;
        if (typeof val === 'string') {
          result = result.replace(`{{${key}}}`, val);
        } else if (typeof val === 'object') {
          result = path ? JSONUtils.find(val, `$.${path}`) : val;
        }
      });
      return result;
    } else {
      const resolved = resolveWorkflowPath(str);
      if (resolved) {
        const val = vars[resolved.key] || this.variables[resolved.key] || this.contextVariables[resolved.key];
        const res = JSONUtils.find(val, `$.${resolved.name}`);
        if (res) {
          return res;
        }
      }
    }

    return str;
  }

  isVariable(str: string) {
    return typeof str === 'string' && Boolean(str.match(/^{{[\w-]*}}+[\w\/]*/g));
  }
}

export const Variables = new AppVariables();
