import { Injectable } from '@angular/core';
import { Observable, throwError, of, BehaviorSubject } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map, catchError, tap, share } from 'rxjs/operators';
import { User } from '../models';
import { environment } from 'src/environments/environment';
import { NotificationService } from 'src/app/shared/services/notification.service';
import { NotificationType } from 'src/app/shared/models';
import { Variables } from 'src/app/shared/app-variables';

interface LoginResponseDto {
  token: string;
  user: User;
}

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  constructor(private httpClient: HttpClient, private notificationService: NotificationService) {
    this.getCurrentUser().subscribe();
  }

  private _user: User;
  private _token: string;
  private _isAuth = new BehaviorSubject<boolean>(false);
  public isAuthenticated$ = this._isAuth.asObservable();

  get token(): string {
    if (!this._token) {
      this._token = JSON.parse(localStorage.getItem('token'));
    }

    return this._token;
  }

  get user(): User {
    return this._user;
  }

  /**
   * Authenticate user.
   * @param credentials User auth credentials.
   */
  login({ email, password }): Observable<LoginResponseDto> {
    return this.httpClient
      .post<LoginResponseDto>(`${environment.apiBaseUrl}/security/authenticate`, { username: email, password: password })
      .pipe(map(r => this.authHandler(r)),
        catchError(res => {
          this.logout();
          this.notificationService.open({
            type: NotificationType.DANGER,
            message: res.error.message || 'Connection error.',
            backdrop: false
          });
          return throwError(res);
        })
      );
  }

  authorize(token: string, saveAs?: string) {
    if (token) {
      const url = `${environment.apiBaseUrl}/security/authenticate-with-access-token/${token}`;
      return this.httpClient.post<LoginResponseDto>(url, null).pipe(
        tap((res) => {
          if (saveAs) {
            Variables.setContextVariable({
              name: saveAs,
              value: res.token
            });
          }
        }),
        catchError(e => throwError(e))
      );
    }

    return this.getCurrentUser().pipe(
      map(user => ({user, token: this.token})));
  }

  logout() {
    localStorage.removeItem('token');
    Variables.removeContextVariable('userInfo');
    Variables.removeContextVariable('authentication_token');
    Variables.setVariable('authentication_token', null);
    this._user = null;
    this._token = null;
    this._isAuth.next(false);
  }

  /**
   * Is user authenticated.
   * @deprecated
   */
  isAuthenticated(): Observable<boolean> {
    return this.isAuthenticated$;
  }

  /**
   * Get current user from session, if exists.
   */
  getCurrentUser(): Observable<User> {
    if (this._user) {
      return of(this._user);
    }

    if (this.token) {
      return this.httpClient.get<User>(`${environment.apiBaseUrl}/security/user`, this.getHttpHeaders()).pipe(
        tap(user => this.authHandler({user, token: this.token})),
        catchError(() => {
          this.logout();
          return throwError('Not authenticated');
        }));
    }

    this.logout();

    return throwError('Not authenticated');
  }


  getHttpHeaders() {
    const token = `Bearer ${this.token}`;

    return {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': token
      })
    };
  }

  private authHandler({user, token}: LoginResponseDto) {
    this._user = user;
    this._token = token;
    Variables.setContextVariable({name: 'userInfo', value: this._user});
    localStorage.setItem('token', JSON.stringify(token));
    this._isAuth.next(true);
    return {user, token};
  }
}
