import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { environment } from 'src/environments/environment';

// Import Containers
import { LoginComponent, AuthGuard } from 'src/app/security';
import { ResetPasswordPageComponent } from 'src/app/security/components/reset-password-page/reset-password-page.component';

export const routes: Routes = [
  { path: '', redirectTo: 'rest-client', pathMatch: 'full' },
  {
    path: 'login',
    component: LoginComponent,
    data: {
      title: 'Login Page'
    }
  },
  {
    path: 'rest-client',
    loadChildren: () => import('./rest-client/rest-client.module').then(m => m.RestClientModule),
    data: {
      title: 'Rest client'
    },
  },
  {
    path: 'forms',
    loadChildren: () => import('./forms-builder/forms-builder.module').then(m => m.FormsBuilderModule),
    data: {
      title: 'Forms builder'
    },
  },
  {
    path: 'reset/:id',
    component: ResetPasswordPageComponent
  },
  {
    path: 'json-editor',
    loadChildren: () => import('./json-editor/json-editor.module').then(m => m.JsonEditorModule),
    data: {
      title: 'Json editor'
    },
  },
  {
    path: 'app/:owner',
    loadChildren: () => import('./custom-app/custom-app.module').then(m => m.CustomAppModule),
    canActivate: [AuthGuard],
    data: {
      title: 'Applications'
    },
  },
  {
    path: 'database',
    loadChildren: () => import('./database-manager/database-manager.module').then(m => m.DatabaseManagerModule),
    canActivate: [AuthGuard],
    data: {
      title: 'Database manager'
    },
  },
  { path: '**', redirectTo: 'rest-client', pathMatch: 'full' }
];


@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: environment.useHash })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
