import { Component, OnInit, Input, ViewChild, ElementRef, SimpleChanges, OnChanges } from '@angular/core';
import { Ace, edit } from 'ace-builds';
import { formatObjToJsonStr } from '../../utils';

@Component({
  selector: 'app-json-preview',
  templateUrl: './json-preview.component.html',
  styleUrls: ['./json-preview.component.scss']
})
export class JsonPreviewComponent implements OnInit, OnChanges {

  @Input() data: any;
  @ViewChild('json', {static: true}) jsonEl: ElementRef;
  jsonEditor: Ace.Editor;
  constructor() { }

  ngOnInit() {
    this.jsonEditor = edit(this.jsonEl.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.data),
      readOnly: true
    });
  }

  ngOnChanges({data}: SimpleChanges) {
    if (!data.firstChange && data.currentValue) {
      this.jsonEditor.setValue(formatObjToJsonStr(this.data));
    }
  }

}
