import { EnsureTableConfig, NotificationActionConfig, AuthorizeConfig } from '../models';
import { IfStatementConfig, CreateObjectConfig, HttpCallConfig, ActionConfig } from '@ngx-dynamic-components/core';

const UI_MODEL = {
  id: 'form',
  type: 'material:flex-container',
  containerProperties: {},
  itemProperties: {
    width: '100%',
    height: '100%',
    padding: '16px',
    margin: '0',
    fxLayoutAlign: 'start|stretch',
    fxLayout: 'column',
    fxLayoutGap: '8px',
    isForm: true
  },
  children: []
};

export const Form = {
  uiModel: UI_MODEL,
  workflowsMap: {
    _var_import_overrides: {
      ownerName: '',
      base_api_url: 'https://worksheets-web-api-test.azurewebsites.net',
      userPublicAccessCode: '12000020PP',
      tableName: ''
    },
    _var: {
      sql_api_url: '{{base_api_url}}/api/sql/sql-shared',
      public_access_code: '{{userPublicAccessCode}}',
      ownerName: '{{ownerName}}',
      tableName: '{{tableName}}'
    },
    design_time: [
      {
        actionType: 'ensureTableWithFields',
        actionName: 'ensure',
        sqlConnectionUrl: '{{sql_api_url}}',
        ownerName: '{{ownerName}}',
        tableName: '{{tableName}}',
        generateFields: 'fromDataModel'
      } as EnsureTableConfig,
      {
        actionType: 'if',
        value: '$ensure-returnValue/status',
        compareTo: 'success',
        trueActions: [],
        falseActions: [
          {
            actionType: 'notification',
            message: '$ensure-returnValue/result/error',
            type: 'danger'
          } as NotificationActionConfig
        ]
      } as IfStatementConfig
    ],
    form_OnInit: [
      {
        actionType: 'ws-login',
        actionName: 'login',
        publicAccessCode: '{{public_access_code}}',
        saveAs: 'bearer_token'
      } as AuthorizeConfig,
      {
        actionType: 'if',
        value: '$login-returnValue/user/userName',
        compareTo: 'Public',
        trueActions: [],
        falseActions: [
          {
            actionType: 'notification',
            message: 'Failed to login.',
            type: 'danger'
          } as NotificationActionConfig
        ]
      } as IfStatementConfig
    ],
    form_OnSubmit: [
      {
        actionType: 'dataModelValidation',
        actionName: 'validation'
      } as ActionConfig,
      {
        actionType: 'if',
        value: '$validation-returnValue/result',
        trueActions: [
          {
            actionType: 'createObject',
            actionName: 'result',
            object: {
              'items': [
                '$dataModel'
              ]
            }
          } as CreateObjectConfig,
          <any>{
            actionType: 'httpCall',
            actionName: 'request',
            url: '{{sql_api_url}}/save/{{ownerName}}/{{tableName}}',
            method: 'POST',
            body: '$result-returnValue',
            headers: {
              'Authorization': 'Bearer {{bearer_token}}'
            }
          } as HttpCallConfig,
          {
            actionType: 'if',
            value: '$request-returnValue/status',
            compareTo: 'success',
            trueActions: [
              {
                actionType: 'notification',
                message: 'Successfully sent.',
                type: 'success'
              } as NotificationActionConfig
            ],
            falseActions: [
              {
                actionType: 'notification',
                message: 'Failed to send. Please try again.',
                type: 'danger'
              } as NotificationActionConfig
            ]
          }
        ]
      }
    ]
  }
};
