import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/security/services/auth.service';
import { LoginService } from 'src/app/security/services/login.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-nav',
  template: `
    <ng-content></ng-content>
    <ul class="nav navbar-nav mr-2">
      <li class="nav-item dropdown" dropdown placement="bottom right" *ngIf="isAuthenticated">
        <a class="nav-link" data-toggle="dropdown" href="#" role="button"
           aria-haspopup="true" aria-expanded="false" dropdownToggle (click)="false">
          <img *ngIf="authService.user.avatar; else userEmail;" [src]="authService.user.avatar" class="img-avatar"/>
          <ng-template #userEmail>{{authService.user.firstName}} {{authService.user.lastName}}</ng-template>
        </a>
        <div class="dropdown-menu dropdown-menu-right" *dropdownMenu aria-labelledby="simple-dropdown">
          <div class="dropdown-header text-center"><strong>Settings</strong></div>
          <a class="dropdown-item disabled" href="#"><i class="fa fa-user"></i> Profile</a>
          <a class="dropdown-item disabled" href="#"><i class="fa fa-wrench"></i> Setting</a>
          <div class="divider"></div>
          <a class="dropdown-item" (click)="onLogout()"><i class="fa fa-lock"></i> Logout</a>
        </div>
      </li>
      <li *ngIf="!isAuthenticated">
        <a class="cursor-pointer" (click)="onLogin()">Login</a>
      </li>
    </ul>
    <ng-content select="[settings]"></ng-content>
  `,
  styles: [`
    .dropdown-item {
      cursor: pointer;
    }
  `]
})
export class NavComponent implements OnInit, OnDestroy {

  isAuthenticated: boolean;
  destroy = new Subject<any>();

  @Input() logoutRedirect: string|boolean;

  constructor(public authService: AuthService, private router: Router, private loginService: LoginService) {}

  ngOnInit() {
    this.authService.isAuthenticated$.pipe(takeUntil(this.destroy)).subscribe(val => {
      this.isAuthenticated = val;
    });
  }

  ngOnDestroy() {
    this.destroy.next();
    this.destroy.complete();
  }

  onLogin() {
    this.loginService.openDialog();
  }

  onLogout() {
    this.authService.logout();
    if (this.logoutRedirect !== false) {
      this.router.navigate([this.logoutRedirect ? this.logoutRedirect : '/']);
    }
  }
}
