import { Component, OnInit, ViewChild, Input, OnDestroy } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { NotificationConfig, NotificationType } from '../../models';

@Component({
  selector: 'app-notification',
  template: `
  <div bsModal #modal="bs-modal" class="modal fade" tabindex="-1" role="dialog"
   aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-sm" role="document">
      <div class="modal-content shadow" *ngIf="config">
        <alert [type]="config.type" class="m-0">
          <button type="button" class="close" data-dismiss="alert" (click)="modal.hide()" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
          {{config.message}}
        </alert>
      </div>
    </div>
  </div>
  `,
  styles: [`
  alert ::ng-deep .alert {
      margin-bottom: 0;
      text-align: center;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  `]
})
export class NotificationComponent implements OnInit, OnDestroy {

  @ViewChild('modal', {static: true}) public modal: ModalDirective;

  @Input()
  config: NotificationConfig;

  private timer: number;

  constructor() { }

  openModal() {
    const useBackdrop = this.config.hasOwnProperty('backdrop') ? this.config.backdrop : this.config.type !== NotificationType.SUCCESS;
    this.modal.config.backdrop = useBackdrop;
    this.modal.config.ignoreBackdropClick = useBackdrop;
    this.modal.show();
  }

  ngOnInit() {
    if (this.config.type === NotificationType.SUCCESS) {
      this.timer = window.setTimeout(() => {
        this.modal.hide();
      }, 3e3);
    }
  }

  ngOnDestroy() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
  }
}
