import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-split-content',
  templateUrl: './split-content.component.html',
  styleUrls: ['./split-content.component.scss']
})
export class SplitContentComponent implements OnInit {

  leftContentSize = 50;
  modeState = {
    left: true,
    right: true
  };

  constructor() { }

  ngOnInit() {
  }

  onModeState(prop: string) {
    this.modeState[prop] = !this.modeState[prop];
    if (this.modeState.left && !this.modeState.right) {
      this.leftContentSize = 100;
    } else if (this.modeState.left && this.modeState.right) {
      this.leftContentSize = 50;
    } else {
      this.leftContentSize = 0;
    }
  }

  resize() {

  }

}
