import { Injectable, Inject, ApplicationRef, Injector, EmbeddedViewRef, ComponentFactoryResolver, Type, ComponentRef } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { DynamicDialogComponent } from 'src/app/shared/components/dynamic-dialog/dynamic-dialog.component';
import { ConfirmationComponent } from '../components/confirmation/confirmation.component';
import { Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  dialogComponentRef: ComponentRef<DynamicDialogComponent>;

  constructor(
    @Inject(DOCUMENT) private _document: Document,
    private componentFactoryResolver: ComponentFactoryResolver,
    private appRef: ApplicationRef,
    private injector: Injector) { }

  open(componentType: Type<any>, options?: object) {
    this.appendModal();
    this.dialogComponentRef.instance.childComponentType = componentType;
    this.dialogComponentRef.instance.options = options || {};
    return this.dialogComponentRef.instance;
  }

  close() {
    if (this.dialogComponentRef) {
      this.dialogComponentRef.instance.modal.hide();
    }
  }

  confirmation(message) {
    const dialog = this.open(ConfirmationComponent);
    dialog.config = {message};
    return new Observable((subscriber) => {
      dialog.init.pipe(
        switchMap(() => dialog.component.confirm)).subscribe((val) => {
          subscriber.next(val);
          subscriber.complete();
        });
    });
  }

  private appendModal() {
    this.dialogComponentRef = this.componentFactoryResolver.resolveComponentFactory(DynamicDialogComponent).create(this.injector);
    this.dialogComponentRef.instance.modal.onHide.subscribe(() => {
      this.dialogComponentRef.destroy();
    });
    this.appRef.attachView(this.dialogComponentRef.hostView);
    const domElem = (this.dialogComponentRef.hostView as EmbeddedViewRef<any>).rootNodes[0] as HTMLElement;

    this._document.body.appendChild(domElem);
    setTimeout(() => {
      this.dialogComponentRef.instance.modal.show();
    });
  }
}
