import { TableDto, MetaDataObject, SqlField, SQLDataTypes, TableColumn, FilterSqlData, Operation } from './models';
import { ObjectType } from '../database-manager/models';
import * as moment from 'moment';


export function formatCamelStr(str: string = ''): string {
  return str.replace(/([a-z0-9])([A-Z])/g, '$1 $2').replace(/_/g, ' ');
}

export function mapTableToValues<T>(table: TableDto): T[] {
  const values = [];
  const hasDataTypes = Boolean(table.dataTypes);
  for (const row of table.rows || []) {
    const value = {} as T;
    for (let i = 0; i < table.fieldNames.length; i++) {
      const fieldName = table.fieldNames[i];
      const dataType = hasDataTypes ? table.dataTypes[i] : null;
      if (dataType === SQLDataTypes.DATE && row[i]) {
        value[fieldName] = new Date(row[i]);
      } else {
        value[fieldName] = row[i];
      }
    }
    values.push(value);
  }
  return values;
}

export function dateToDtoString(d: Date): string {
  const date = new Date(d.getTime());
  date.setMinutes(date.getMinutes() - date.getTimezoneOffset());
  return date.toISOString().replace('T00:00:00.000Z', '');
}

export function parseDatetimeOrNull(value: string | Date): Date {
  if (!value) return null;
  if (value instanceof Date && !isNaN(value.valueOf())) return value;
  // only string values can be converted to Date
  if (typeof value !== "string") return null;

  const strValue = String(value);
  if (!strValue.length) return null;

  const parseMonth = (mm: string): number => {
    if (!mm || !mm.length) {
      return NaN;
    }

    const m = parseInt(mm);
    if (!isNaN(m)) {
      return m - 1;
    }

    // make sure english months are coming through
    if (mm.startsWith('jan')) return 0;
    if (mm.startsWith('feb')) return 1;
    if (mm.startsWith('mar')) return 2;
    if (mm.startsWith('apr')) return 3;
    if (mm.startsWith('may')) return 4;
    if (mm.startsWith('jun')) return 5;
    if (mm.startsWith('jul')) return 6;
    if (mm.startsWith('aug')) return 7;
    if (mm.startsWith('sep')) return 8;
    if (mm.startsWith('oct')) return 9;
    if (mm.startsWith('nov')) return 10;
    if (mm.startsWith('dec')) return 11;

    return NaN;
  };

  const correctYear = yy => {
    if (yy < 100) {
      return yy < 68 ? yy + 2000 : yy + 1900
    } else {
      return yy;
    }
  };

  const validDateOrNull =
    (yyyy: number, month: number, day: number, hours: number, mins: number, ss: number): Date => {
      if (month > 11 || day > 31 || hours >= 60 || mins >= 60 || ss >= 60) return null;

      const d = new Date(yyyy, month, day, hours, mins, ss, 0);
      return !isNaN(d.valueOf()) ? d : null;
    };

  const strTokens = strValue.replace('T', ' ').toLowerCase().split(/[: /-]/);
  const dt = strTokens.map(parseFloat);

  // try ISO first
  let d = validDateOrNull(dt[0], dt[1] - 1, dt[2], dt[3] || 0, dt[4] || 0, dt[5] || 0);
  if (d) return d;

  // then UK
  d = validDateOrNull(correctYear(dt[2]), parseMonth(strTokens[1]), dt[0], dt[3] || 0, dt[4] || 0, dt[5] || 0);
  if (d) return d;

  // then US
  d = validDateOrNull(correctYear(dt[2]), parseMonth(strTokens[0]), correctYear(dt[1]), dt[3] || 0, dt[4] || 0, dt[5] || 0);
  if (d) return d;

  return null;
}

export function parseNumberOrNull(value: string | number): number {
  if (typeof value === 'number') {
    return value;
  }

  if (!value || typeof value !== 'string') {
    return null;
  }

  const res = parseFloat(value.replace(/,/g, ''))
  return !isNaN(res) ? res : null;
}

export function replaceAll(text: string, searchValue: string, replaceValue: string = ''): string {
  return text.replace(new RegExp(searchValue, 'g'), replaceValue || '');
}

export function parseBooleanOrNull(val: boolean | string): boolean {
  const trulyVals = ['1', 'yes', 'true', 'on'];
  const falsyVals = ['0', 'no', 'false', 'off'];
  const checkVal = val.toString().toLowerCase();

  if (trulyVals.includes(checkVal)) {
    return true;
  }

  if (falsyVals.includes(checkVal)) {
    return false;
  }

  return null;
}

// what type parameter 'values' is ????
export function mapValuesToTable(values: Object[]): TableDto {
  const tableDto: TableDto = {
    fieldNames: [],
    rows: []
  };

  if (!values.length) {
    return tableDto;
  }

  tableDto.fieldNames = Object.keys(values[0]);
  tableDto.rows = values
    .map(rowValues => Object.values(rowValues)
      .map(v => v instanceof Date ? dateToDtoString(v) : v));
  return tableDto;
}

export function formatObjToJsonStr(obj: any): string {
  if (obj === null || obj === undefined) {
    return '';
  }

  if (typeof obj === 'string') {
    return obj;
  }
  return JSON.stringify(obj, null, '\t');
}

export function transposeData(data: any[]) {
  if (!data) {
    return null;
  }

  return Object.keys(data[0]).map(key => {
    const res: { [key: string]: any } = {};
    data.forEach((item, i) => {
      if (i === 0) {
        res.fieldName = key;
      }

      res['row' + i] = item[key];
    });
    return res;
  });
}

export function getDomain(url: string): string {
  let match = url.match(/:\/\/(www[0-9]?\.)?(.[^/:]+)/i);
  if (match != null && match.length > 2 && typeof match[2] === 'string' && match[2].length > 0) {
    return match[2];
  }

  match = url.match(/^{{\w+}}/i);
  if (match && match.length) {
    return match[0];
  }

  return null;
}

export function verifyFields(fields: TableColumn[], comparedFields: TableColumn[]) {
  return fields.length === comparedFields.length && fields.every(f => {
    const comparedField = comparedFields.find(cf => cf.fieldName === f.fieldName);
    if (!comparedField) {
      return false;
    }
    return compareColumns(comparedField, f);
  });
}

export function compareFields(a: SqlField, b: SqlField): boolean {
  return ['dataType', 'fieldName', 'isNullable', 'size'].every(prop => a[prop] === b[prop]);
}

export function compareSqlObjects(a: MetaDataObject, b: MetaDataObject) {
  if (!(a && b && a.objectName === b.objectName && a.objectOwner === b.objectOwner)) {
    return false;
  }
  if (a.objectType === ObjectType.View) {
    return a.sqlText === b.sqlText;
  }
  return verifyFields(getTableColumns(a), getTableColumns(b));
}

export function compareColumns(a: TableColumn, b: TableColumn): boolean {
  return ['dataType', 'fieldName', 'isKey', 'size'].every(prop => a[prop] === b[prop]);
}

export function generateFields(arr: object | Array<string>): SqlField[] {
  if (!Array.isArray(arr)) {
    arr = Object.keys(arr);
  }
  return (arr as Array<string>).map(prop => ({
    fieldName: prop,
    dataType: SQLDataTypes.TEXT,
    size: 100,
    isNullable: false
  }));
}

export function getTableColumns(obj: MetaDataObject): TableColumn[] {
  const primaryKeys = (obj.primaryKeys || []).map(o => o.fieldName);
  return obj.fields.map(f => ({ ...f, isKey: primaryKeys.includes(f.fieldName) }));
}

export function getErrorMessage(e: any, msg?: string): string {
  if (typeof e === 'string') {
    return e;
  }
  let error = e.error;
  if (typeof e.error === 'object' && e.error !== null) {
    error = [e.error.title, Object.values(e.error.errors)].join('\n');
    return error;
  } else if (typeof error === 'string') {
    return error;
  } else {
    return e.message || msg;
  }
}
export function deepClone(obj: any): any {

  if (obj == null || obj == undefined) {
    return obj;
  }

  if (obj instanceof Date) {
    return new Date(obj.getTime());
  }

  if (Array.isArray(obj)) {
    return obj.map(v => deepClone(v))
  }

  if (typeof obj === 'object') {
    const clone = {};
    for (const propName in obj) {
      const propValue = obj[propName];

      if (propValue == null || propValue == undefined) {
        clone[propName] = propValue;
      } else if (propValue instanceof Date) {
        clone[propName] = new Date(propValue.getTime());
      } else if (Array.isArray(propValue)) {
        clone[propName] = propValue.map(v => deepClone(v));
      } else if (typeof (propValue) == "object") {
        clone[propName] = deepClone(propValue);
      } else {
        clone[propName] = propValue;
      }
    }
    return clone;
  }

  return obj;
}


export function getFilters(currentFilters: Object, currentFildName: string = null): FilterSqlData[] {
  if (!currentFilters) {
    return null;
  }
  const filters = Object.entries(currentFilters).
    filter(([fieldName, value]) => hasValue(value) && fieldName !== currentFildName).
    map(([fieldName, value]) => ({
      AndOr: 'And',
      fieldName,
      operation: Operation.In,
      value
    }));
  return hasValue(filters) ? filters : null;
}

export function hasValue(value: any): boolean {
  return Array.isArray(value) && value.length || !Array.isArray(value) && value;
}

/**
 * Formats data to array/object with primitive values.
 */
export function formatToPrimitive(data: any): Object[] | Object | string | number | boolean {
  if (Array.isArray(data)) {
    return data.map(item => {
      return formatToPrimitiveObject(item);
    });
  }

  if (data instanceof Date) {
    return dateToDtoString(data);
  }

  if (typeof data === 'object') {
    return JSON.stringify(data);
  }

  return data;
}

/**
 * Formats object values to primitives.
 */
function formatToPrimitiveObject(obj: Object): Object {
  const res = { ...obj };
  Object.entries(res).forEach(([key, val]) => {
    if (val instanceof Date) {
      res[key] = dateToDtoString(val);
    } else if (typeof val === 'object') {
      res[key] = JSON.stringify(val);
    }
  });
  return res;
}

export function formatWijmoDate(v: Date | string, displayFormat?: string): string {
  if (!displayFormat) {
    displayFormat = 'dd/MM/yyyy';
  }
  return moment(v).format(displayFormat.replace('yyyy', 'YYYY').replace('dd', 'DD'));
}
