import { DataType } from 'wijmo/wijmo';

export type DataTypes = 'Short Text' | 'Long Text' | 'Text MAX' | 'Whole Number' | 'Float Number' | 'True/False' | 'Date Time';
export const DATA_TYPES = ['Text', 'Short Text', 'Long Text', 'Whole Number', 'Float Number', 'True/False', 'Date Time', 'Text MAX'];

export interface TableDto {
  dataTypes?: SQLDataTypes[];
  fieldNames: string[];
  rows: any[][];
}

export interface DataObjectLink {
  objectId: number;
  objectOwner: string;
  objectName: string;
  objectType: 'Table' | 'View' | 'Query';
  folder: string;
}

export interface FieldInfo {
  fieldName: string;
  dataType: DataTypes;
  isKey?: boolean;
}

export interface DataObject extends DataObjectLink  {
  fields: FieldInfo[];
  details: object;
}

export enum SQLDataTypes {
  TEXT = 'text',
  CHAR = 'char',
  VARCHAR = 'varchar',
  NVARCHAR = 'nvarchar',
  BINARY = 'binary',
  VARBINARY = 'varbinary',

  BIT = 'bit',
  TINYINT = 'tinyint',
  SMALLINT = 'smallint',
  INT = 'int',
  BIGINT = 'bigint',
  DECIMAL = 'decimal',
  REAL = 'real',
  NUMERIC = 'numeric',
  FLOAT = 'float',

  DATE = 'datetime2'
}

export const SqlTypesToWijmoMap = {
  [SQLDataTypes.TEXT]: DataType.String,
  [SQLDataTypes.CHAR]: DataType.String,
  [SQLDataTypes.VARCHAR]: DataType.String,
  [SQLDataTypes.NVARCHAR]: DataType.String,
  [SQLDataTypes.BINARY]: DataType.String,
  [SQLDataTypes.VARBINARY]: DataType.String,

  [SQLDataTypes.DATE]: DataType.Date,

  [SQLDataTypes.DECIMAL]: DataType.Number,
  [SQLDataTypes.FLOAT]: DataType.Number,
  [SQLDataTypes.NUMERIC]: DataType.Number,
  [SQLDataTypes.REAL]: DataType.Number,
  [SQLDataTypes.SMALLINT]: DataType.Number,
  [SQLDataTypes.BIGINT]: DataType.Number,
  [SQLDataTypes.INT]: DataType.Number,

  [SQLDataTypes.BIT]: DataType.Boolean
};

export const WijmoMapToSqlTypes = {
  Date: SQLDataTypes.DATE,
  String: SQLDataTypes.NVARCHAR,
  Number: SQLDataTypes.NUMERIC,
  Boolean: SQLDataTypes.BIT
};

export const SqlTypesSizes = {
  [SQLDataTypes.TEXT]: 8000,
  [SQLDataTypes.CHAR]: 8000,
  [SQLDataTypes.VARCHAR]: 8000,
  [SQLDataTypes.NVARCHAR]: 50,
};
