import { SQLDataTypes } from './dto';
import { DataType } from 'wijmo/wijmo';
import { DataMap } from 'wijmo/wijmo.grid';

export interface SqlPrimaryKey {
  indexType?: 'CLUSTERED';
  sortOrder?: 'ASC' | 'DESC';
  name?: string;
  fieldName: string;
}

export interface TableColumn {
  fieldName: string;
  dataType: SQLDataTypes;
  size?: number;
  isKey?: boolean;
}

export interface ColumnFilter {
  bindingName: string;
  dataType: SQLDataTypes|DataType;
  selected: any;
  items: ColumnFilterData[];
}

export interface ColumnFilterData {
  count: number;
  value: any;
  selected: boolean;
}

export interface MetaDataObject {
  fields: SqlField[];
  details?: object;
  objectId: number;
  objectOwner: string;
  objectName: string;
  objectType: string;
  folder?: string;
  primaryKeys?: SqlPrimaryKey[];
  sqlText?: string;
}

export interface SqlObject {
  objectId: number;
  objectName: string;
  objectOwner: string;
  objectType: string;
}

export interface SqlTableData {
  resultType: 'Table';
  table: {
    fieldNames: string[]
    rows: any[]
  };
  fields: {
    fieldName: string;
    dataType: string;
  }[];
}

export interface SqlField {
  fieldName: string;
  dataType: SQLDataTypes;
  size?: number;
  isNullable?: boolean;
}

export interface ObjectOwner {
  objectName: string;
  objectOwner: string;
  objectType?: string;
}

export interface DBConnection {
  connectorId: number;
  connectorName: string;
}

export interface SqlSaveStatus {
  inserted: number;
  updated: number;
  deleted: number;
  itemsToSaveCount?: number;
  savedItemsCount?: number;
}

export enum Operation {
  In = 'IN',
  NotIn = 'NotIn',
  Equal = 'Equal',
  NotEqual = 'NotEqual',
  GreaterThan = 'GreaterThan',
  GreaterOrEqual = 'GreaterThanOrEqual',
  LessThan = 'LessThan',
  LessOrEqual = 'LessThanOrEqual',
  Like = 'Like',
  NotLike = 'NotLike'
}

export interface FilterSqlData {
  AndOr: string;
  fieldName: string;
  operation: Operation;
  value: any | any[];
}

export interface SelectBody {
  fields?: string[];
  filters?: FilterSqlData[];
  limitRowsCount?: number;
  skipRowsCount?: number;
}
