import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { RegistrationForm, User, ResetPassword } from '../models';
import { NotificationService } from 'src/app/shared/services/notification.service';
import { NotificationType } from 'src/app/shared/models';
import { environment } from 'src/environments/environment';

@Injectable({providedIn: 'root'})
export class RegistrationService {
  constructor(private http: HttpClient, private notificationService: NotificationService) {}

  register(data: RegistrationForm) {
    return this.http.post<User>(`${environment.apiBaseUrl}/security/register`, data).pipe(
      map(res => {
        this.notificationService.open({
          type: NotificationType.SUCCESS,
          message: 'Successfully registered!'
        });
        return res;
      }),
      catchError(res => {
        this.notificationService.open({
          type: NotificationType.DANGER,
          message: res.error.message || res.message
        });
        return throwError(res);
      }));
  }

  resetRequest(userName: string) {
    return this.http.get(`${environment.apiBaseUrl}/security/reset-password-request/${userName}`, {
      responseType: 'text'
    }).pipe(
      map(res => {
        this.notificationService.open({
          type: NotificationType.SUCCESS,
          message: res
        });
        return res;
      }),
      catchError(res => {
        this.notificationService.open({
          type: NotificationType.DANGER,
          message: res.error || res.message
        });
        return throwError(res);
      }));
  }

  resetPassword(data: ResetPassword) {
    return this.http.put(`${environment.apiBaseUrl}/security/reset-password`, data, {
      responseType: 'text'
    }).pipe(
      map(res => {
        this.notificationService.open({
          type: NotificationType.SUCCESS,
          message: res || 'Password reset successfully'
        });
        return res;
      }),
      catchError(res => {
        this.notificationService.open({
          type: NotificationType.DANGER,
          message: res.error || res.message
        });
        return throwError(res);
      }));
  }
}
