import { Component, ComponentRef, Type, ComponentFactoryResolver, ViewChild, AfterViewInit,
  ViewContainerRef, EventEmitter, Output, Input, OnInit } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-dynamic-dialog',
  template: `
    <div bsModal #modal="bs-modal" class="modal fade" tabindex="-1" role="dialog"
      aria-labelledby="myModalLabel" aria-hidden="true">
      <div class="modal-dialog" [ngClass]="[options.class || '']" role="document">
        <div class="modal-content shadow h-100">
				  <ng-template #dialogContent></ng-template>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .modal-content > ::ng-deep * {
      height: 100%;
    }

    .modal-dialog.full-height {
      height: calc(100% - 56px);
    }
  `]
})
export class DynamicDialogComponent implements OnInit {

  componentRef: ComponentRef<any>;

  @ViewChild('dialogContent', { static: false, read: ViewContainerRef }) insertionPoint: ViewContainerRef;
  @ViewChild('modal', {static: true}) public modal: ModalDirective;
  @Output() init = new EventEmitter<any>();
  @Input() config;
  @Input() options;
  childComponentType: Type<any>;

  get component() {
    if (this.componentRef) {
      return this.componentRef.instance;
    }
  }

  constructor(private componentFactoryResolver: ComponentFactoryResolver) { }

  ngOnInit() {
    window.requestAnimationFrame(() => {
      this.loadChildComponent(this.childComponentType);
      this.init.emit();
    });
  }

  loadChildComponent(componentType: Type<any>) {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentType);
    this.insertionPoint.clear();
    this.componentRef = this.insertionPoint.createComponent(componentFactory);
    this.componentRef.instance.config = this.config;
    this.modal.config = this.options;
    this.componentRef.instance.modalRef = this.modal;
  }
}
