import { Component, OnInit, ViewChild, ElementRef, Input, SimpleChanges, OnChanges } from '@angular/core';
import { Ace, edit } from 'ace-builds';
import { formatObjToJsonStr } from '../../utils';
import { fromEvent } from 'rxjs';
import { debounceTime, map } from 'rxjs/operators';
import { WorkflowConfig } from '@ngx-dynamic-components/core';
import { Variables } from '../../app-variables';

@Component({
  selector: 'app-content-explorer',
  templateUrl: './content-explorer.component.html',
  styleUrls: ['./content-explorer.component.scss']
})
export class ContentExplorerComponent implements OnInit, OnChanges {

  @Input() isTextAvailable: boolean;
  @Input() data: any;
  @Input() uiModel: any;
  @Input() formatter: boolean;
  @ViewChild('contentJSON', {static: true}) contentEl: ElementRef;
  dataModel: any;
  jsonContentSize = 50;
  contentEditor: Ace.Editor;
  formatted = true;
  modeState = {
    json: true,
    designer: true
  };
  private isReadonly = false;
  private isVariable = false;

  explorerUIModel = {
    type: 'rest-client:grid-object-explorer',
    containerProperties: {},
    itemProperties: {
      dataSource: null,
      isReadOnly: false,
      allowDelete: true,
      allowAddNew: true,
      host: 'json-editor-object-explorer',
      selectionMode: 4
    }
  };

  workflow: WorkflowConfig = {
    failOnError: true,
    include: ['@common'],
    vars: {},
    workflowsMap: {
      init: []
    },
    consts: {}
  };

  constructor() { }

  ngOnInit() {
    if (this.formatter === undefined) {
      this.formatter = true;
    }
    if (this.uiModel) {
      this.explorerUIModel = this.uiModel;
    }
    this.dataModel = this.data;
    if (this.dataModel) {
      this.explorerUIModel.itemProperties.dataSource = this.dataModel;
    }

    this.initContentEditor();
  }

  private initContentEditor() {
    this.contentEditor = edit(this.contentEl.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.data),
      readOnly: this.isReadonly
    });

    fromEvent(this.contentEditor, 'change').pipe(
      debounceTime(500),
      map(() => {
        return this.getDataObject();
      })).subscribe(async ({value, parsedValue}) => {
        this.dataModel = this.isVariable ? value : parsedValue;
        this.explorerUIModel.itemProperties.dataSource = parsedValue;
      });
  }

  ngOnChanges({data}: SimpleChanges) {
    if (!data.firstChange && data.currentValue !== this.dataModel) {
      const {parsedValue, value} = this.getDataObject(data.currentValue || null);
      this.dataModel = this.isVariable ? value : (parsedValue || value);
      this.explorerUIModel.itemProperties.dataSource = parsedValue;
      this.onDataModelChanged(this.dataModel);
    }
  }

  get showGrid() {
    return this.dataModel && typeof this.dataModel === 'object';
  }

  resize() {
    this.contentEditor.resize();
  }

  onDataModelChanged(dataModel) {
    this.contentEditor.setValue(this.formatted ? formatObjToJsonStr(dataModel) : JSON.stringify(dataModel), -1);
  }

  setReadOnly(state: boolean) {
    if (this.isReadonly !== state) {
      this.explorerUIModel.itemProperties.isReadOnly = state;
      this.isReadonly = state;
      if (this.contentEditor) {
        this.contentEditor.setReadOnly(state);
      }
    }
  }

  onModeState(prop: string) {
    this.modeState[prop] = !this.modeState[prop];
    if (this.modeState.json && !this.modeState.designer) {
      this.jsonContentSize = 100;
    } else if (this.modeState.json && this.modeState.designer) {
      this.jsonContentSize = 50;
    } else {
      this.jsonContentSize = 0;
    }
  }

  formatJSON(format = true) {
    try {
      const value = JSON.parse(this.contentEditor.getValue());
      if (format) {
        this.contentEditor.setValue(formatObjToJsonStr(value), -1);
      } else {
        this.contentEditor.setValue(JSON.stringify(value), -1);
      }
      this.formatted = format;
    } catch (e) {
      console.error(e);
      this.formatted = false;
    }
  }

  isValid() {
    return true; // this.isVariable || JSON.parse(this.dataModel);
  }

  private getDataObject(value = this.contentEditor.getValue()) {
    return this.isTextAvailable ? this.getAnyObject(value) : this.getJsonObject(value);
  }

  private getJsonObject(value) {
    let parsedValue = null;
    try {
      parsedValue = JSON.parse(value);
    } catch (e) {}
    return {value, parsedValue};
  }

  private getAnyObject(value) {
    let parsedValue;
    this.isVariable = Variables.isVariable(value);

    if (this.isVariable) {
      this.contentEditor.session.setMode('ace/mode/text');
      const resolved = Variables.resolve(value);
      if (typeof resolved === 'string') {
        try {
          parsedValue = JSON.parse(resolved);
        } catch (e) {}
      } else if (typeof resolved === 'object') {
        parsedValue = resolved;
      }
    } else {
      this.contentEditor.session.setMode('ace/mode/json');
      if (typeof value === 'object') {
        parsedValue = value;
      } else {
        try {
          parsedValue = JSON.parse(value);
        } catch (e) {
          this.contentEditor.session.setMode('ace/mode/text');
        }
      }
    }

    return {value, parsedValue};
  }
}
