import { Component, Output, EventEmitter } from '@angular/core';
import { RegistrationService } from '../../services/registration.service';
import { DialogState } from '../../models';

@Component({
  selector: 'app-reset-password-request-form',
  template: `
  <div class="card mb-0">
    <div class="card-body mx-4 mt-4 mb-0">
      <form #resetForm="ngForm">
        <h1>Request password reset</h1>
        <div class="form-group row my-4 mx-0">
          <label for="request-name" class="col-form-label px-0 col-sm-3">Enter UserName or e-mail</label>
          <div class="flex-fill">
            <input type="text" class="form-control" [(ngModel)]="userName"
              #user="ngModel" name="userName" placeholder="Username" autocomplete="username" required>
            <div *ngIf="user.invalid && (user.dirty || user.touched)" class="alert alert-danger py-1 px-2 m-0 w-100">
              <div *ngIf="user.errors.required">UserName or e-mail is required.</div>
            </div>
          </div>
        </div>
      </form>
    </div>
    <div class="card-footer p-4">
      <div class="row m-0 px-4">
        <button type="button" (click)="onSubmit()" [disabled]="!resetForm.valid" class="btn btn-primary">Reset</button>
        <button type="button" (click)="action.emit(DialogState.LOGIN)" class="btn btn-light ml-2">Cancel</button>
      </div>
    </div>
  </div>
  `,
  styles: []
})
export class ResetPasswordRequestFormComponent {

  userName: string;
  DialogState = DialogState;
  @Output() requested = new EventEmitter();
  @Output() action = new EventEmitter<DialogState>();

  constructor(private registrationService: RegistrationService) { }

  onSubmit() {
    this.registrationService.resetRequest(this.userName).subscribe(res => {
      this.requested.emit();
    });
  }
}
