import { Component, OnInit, Input, ContentChild, TemplateRef, SimpleChanges, OnChanges, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-list-view',
  templateUrl: './list-view.component.html',
  styleUrls: ['./list-view.component.scss']
})
export class ListViewComponent implements OnInit, OnChanges {

  @Input() list: any[];
  @Input() selected;
  @Input() group;
  @Input() orderBy;
  @Input() comparator: (x, y) => boolean;
  @Input() searchFn: (str: string, i: any) => boolean;
  @ContentChild(TemplateRef, {static: true}) itemTemplate: TemplateRef<any>;
  @ContentChild('icon', {static: true}) iconTemplate: TemplateRef<any>;
  @Output() select = new EventEmitter();
  groups = [];
  noGroupList = [];
  selectedItem;
  searchState = {};
  activeSearch: string;

  constructor() { }

  ngOnInit() {
    this.selectedItem = this.selected;
    this.orderList();
    this.groups = this.getGroups();
  }

  ngOnChanges({ group, list }: SimpleChanges) {
    if (group && group.previousValue !== group.currentValue && group.currentValue) {
      this.groups = this.getGroups();
    }

    if (list && list.previousValue !== list.currentValue && !list.firstChange) {
      this.orderList();
      this.groups = this.getGroups();
    }
  }

  isActive(item) {
    if (this.comparator) {
      return this.comparator(this.selectedItem, item);
    }

    if (this.selectedItem && this.selectedItem.id) {
      return item.id === this.selectedItem.id;
    }
    return item === this.selectedItem;
  }

  onSearch(groupValue: string, evt: any) {
    const searchStr = evt.target.value.toLowerCase();
    this.searchState[groupValue] = searchStr;
    evt.stopPropagation();
    const group = this.groups.find(g => g.value === groupValue);
    group.filteredList = group.list.filter(i => {
      if (typeof this.searchFn === 'function') {
        return this.searchFn(searchStr, i);
      }
      return Object.values(i).join('').toLowerCase().includes(searchStr);
    });
  }

  selectItem(item) {
    this.selectedItem = item;
  }

  onSelect(item) {
    this.selectedItem = item;
    this.select.emit(item);
  }

  onSearchState(prop: string, evt: Event) {
    this.activeSearch = prop;
    evt.stopPropagation();
  }

  private orderList() {
    if (this.orderBy && this.list) {
      this.list.sort((a, b) => {
        if (typeof this.orderBy === 'string') {
          return this.getValue(this.orderBy, b) - this.getValue(this.orderBy, a);
        }

        if (this.orderBy.type === 'date') {
          return new Date(this.getValue(this.orderBy.key, b)).getTime() -
            new Date(this.getValue(this.orderBy.key, a)).getTime();
        }
      });
    }
  }

  private getGroups(): {list: [], value: string}[] {
    if (this.group && this.list) {
      const groups = {};
      this.noGroupList = [];
      this.list.forEach((item) => {
        const groupValue = this.getValue(this.group, item);
        if (groupValue) {
          if (!groups[groupValue]) {
            groups[groupValue] = {value: groupValue, list: []};
            groups[groupValue].filteredList = groups[groupValue].list;
          }
          groups[groupValue].list.push(item);
        } else {
          this.noGroupList.push(item);
        }
      });

      return Object.values(groups);
    }
    return [];
  }

  private getValue(prop: string | Function, item) {
    if (typeof prop === 'function') {
      return prop(item);
    }
    const subProps = prop.split('.');
    return subProps.reduce((prev, k) => {
      return prev[k];
    }, item);
  }

}
