import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ModalModule } from 'ngx-bootstrap/modal';

import { LoginComponent } from './components/login-page/login.component';
import { AuthGuard } from './components/login-page/auth.guard';
import { AuthService } from './services/auth.service';
import { LoginService } from './services/login.service';
import { LoginFormComponent } from './components/login-form/login-form.component';
import { LoginDialogComponent } from './components/login-dialog/login-dialog.component';
import { RegisterFormComponent } from './components/register-form/register-form.component';
import { ResetPasswordRequestFormComponent } from './components/reset-password-request-form/reset-password-request-form.component';
import { ResetPasswordPageComponent } from './components/reset-password-page/reset-password-page.component';
import { RouterModule } from '@angular/router';

@NgModule({
  declarations: [LoginComponent, LoginFormComponent, LoginDialogComponent, RegisterFormComponent,
    ResetPasswordRequestFormComponent, ResetPasswordPageComponent],
  imports: [
    RouterModule,
    CommonModule,
    FormsModule,
    ModalModule.forRoot()
  ],
  entryComponents: [LoginDialogComponent]
})
export class SecurityModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SecurityModule,
      providers: [ AuthService, AuthGuard, LoginService ]
    };
  }
}
