import { Component, OnInit, ViewChild, Input } from '@angular/core';
import { ModalDirective } from 'ngx-bootstrap';
import { UIModel, WorkflowConfig } from '@ngx-dynamic-components/core';

@Component({
  selector: 'app-dynamic-modal',
  template: `
  <div bsModal #modal="bs-modal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h4 class="modal-title">{{title}}</h4>
          <button type="button" class="close" (click)="modal.hide()" aria-label="Close">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
        <ngx-dynamic-component *ngIf="viewConfig"
          [uiModel]='viewConfig.uiModel'
          [dataModel]='viewConfig.dataModel'></ngx-dynamic-component>
        </div>
      </div>
    </div>
  </div>
  `,
  styles: []
})
export class DynamicModalComponent implements OnInit {

  @ViewChild('modal', {static: false}) public modal: ModalDirective;

  @Input()
  viewConfig: {uiModel: UIModel, dataModel: any, workflowConfig: WorkflowConfig, appContext: any};

  @Input()
  title: string;

  constructor() { }

  openModal() {
    this.modal.show();
  }

  ngOnInit() {
  }

}
