import { Component, OnInit, AfterViewInit, ViewChild, Output, ElementRef, EventEmitter, Input } from '@angular/core';
import { Ace, edit } from 'ace-builds';
import { formatObjToJsonStr } from 'src/app/shared/utils';
import { ModalDirective } from 'ngx-bootstrap';
import { PageInfo } from 'src/app/shared/models';

@Component({
  selector: 'app-page-config',
  templateUrl: './page-config.component.html',
  styleUrls: ['./page-config.component.scss']
})
export class PageConfigComponent implements OnInit, AfterViewInit {

  @Output() save = new EventEmitter<PageInfo>();
  @Input() config: PageInfo;
  @Input() modalRef: ModalDirective;
  @ViewChild('uiModelEl', {static: false}) uiModelEl: ElementRef;
  @ViewChild('workflowEl', {static: false}) workflowEl: ElementRef;

  uiModelEditor: Ace.Editor;
  workflowEditor: Ace.Editor;
  error: string;
  formatted = true;
  pageInfo = {
    title: null,
    description: null,
    pageId: null,
    pageName: null
  };

  constructor() { }

  ngOnInit() {
    Object.keys(this.pageInfo).forEach(key => {
      this.pageInfo[key] = this.config[key];
    });
    this.error = null;
  }

  ngAfterViewInit() {
    this.uiModelEditor = edit(this.uiModelEl.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.config.uiModel)
    });

    this.workflowEditor = edit(this.workflowEl.nativeElement, {
      mode: 'ace/mode/json',
      autoScrollEditorIntoView: true,
      value: formatObjToJsonStr(this.config.workflowsMap)
    });
  }

  savePage() {
    try {
      const uiModel = JSON.parse(this.uiModelEditor.getValue());
      const workflowsMap = JSON.parse(this.workflowEditor.getValue());
      this.save.emit({
        uiModel,
        workflowsMap,
        ...this.pageInfo
      });
      this.modalRef.hide();
    } catch (e) {
      this.error = e.message;
    }
  }

  formatJSON(format = true) {
    try {
      const uiModel = JSON.parse(this.uiModelEditor.getValue());
      const workflowsMap = JSON.parse(this.workflowEditor.getValue());
      if (format) {
        this.uiModelEditor.setValue(formatObjToJsonStr(uiModel), -1);
        this.workflowEditor.setValue(formatObjToJsonStr(workflowsMap), -1);
      } else {
        this.uiModelEditor.setValue(JSON.stringify(uiModel), -1);
        this.workflowEditor.setValue(JSON.stringify(workflowsMap), -1);
      }
      this.formatted = format;
      this.error = null;
    } catch (e) {
      console.error(e);
      this.error = e;
      this.formatted = false;
    }
  }
}
