import { Injectable } from '@angular/core';
import { LocalSettings } from '../models';

@Injectable({providedIn: 'root'})
export class LocalSettingsService {
  private readonly initialSettings: LocalSettings = {
    requests: {},
    sidebar: {}
  };

  getRequestPanelSize(request: {method: string, url: string}) {
    const settings = this.getLocalSettings();
    return settings.requests[`${request.method}-${request.url}`];
  }

  setRequestPanelSize(request: {method: string, url: string}, sizes: (number|string)[]) {
    if (request.url && sizes.length) {
      const settings = this.getLocalSettings();
      settings.requests[`${request.method}-${request.url}`] = sizes;
      localStorage.setItem('localSettings', JSON.stringify(settings));
    }
  }

  setSidebar(appName: string, sidebar: boolean) {
    const settings = this.getLocalSettings();
    settings.sidebar[appName] = sidebar;
    localStorage.setItem('localSettings', JSON.stringify(settings));
  }

  getSidebar(appName: string) {
    const settings = this.getLocalSettings();
    return settings.sidebar[appName];
  }

  private getLocalSettings(): LocalSettings {
    try {
      const localSettings = localStorage.getItem('localSettings') || JSON.stringify(this.initialSettings);
      const settings = JSON.parse(localSettings) as LocalSettings;
      return settings;
    } catch (e) {
      console.error(e);
      return {...this.initialSettings};
    }
  }
}
