import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { PageShortInfo } from '../../models';
import { ModalDirective } from 'ngx-bootstrap';

@Component({
  selector: 'app-page-info',
  templateUrl: './page-info.component.html',
  styleUrls: ['./page-info.component.scss']
})
export class PageInfoComponent implements OnInit {

  @Input() config: PageShortInfo;
  @Input() modalRef: ModalDirective;
  @Output() save = new EventEmitter<PageShortInfo>();

  pageInfo: PageShortInfo = {
    title: null,
    description: null,
    pageId: null,
    pageName: null
  };

  constructor() { }

  ngOnInit() {
    Object.keys(this.pageInfo).forEach(key => {
      this.pageInfo[key] = this.config[key];
    });
  }

  savePage() {
    this.save.emit(this.pageInfo);
    this.modalRef.hide();
  }

}
