import { Component, Input } from '@angular/core';
import { MenuItem } from '../../models';

@Component({
  selector: 'app-page-header',
  template: `
    <div class="container">
      <div class='page-header'>
          <div class='btn-toolbar pull-right'>
              <div class='btn-group'>
                <button *ngFor="let cmd of headerCommands"
                  type="button" class="btn pull-right btn-secondary"
                  [title]="cmd.title || ''"
                  (click)="onCommandClick(cmd)">{{cmd.label}}</button>
              </div>
          </div>
          <h2 class="h2"><i class="mr-2" [ngClass]="iconCssClass"></i>{{title}}</h2>
      </div>
    </div>
  `,
  styles: [`
    :host {
      padding: 0.75rem 1rem;
      list-style: none;
      background-color: #fff;
      border-bottom: 1px solid #c8ced3;
      display: block;
      margin: 0 -30px 24px -30px;
    }

    h2 {
      margin: 0;
    }
  `]
})
export class PageHeaderComponent {

  @Input()
  title: string;

  @Input()
  icon: string;

  @Input()
  headerCommands: MenuItem[];

  get iconCssClass() {
    return this.icon || 'icon-picture icons font-2xl';
  }

  async onCommandClick(cmd: MenuItem) {
    await cmd.command(cmd);
  }
}
