import { Component, OnInit, Input } from '@angular/core';

import { DeviceDetectorService } from 'ngx-device-detector';
import { environment } from 'src/environments/environment';
import { LayoutConfig } from '../../models';
import { LocalSettingsService } from '../../services/local-settings.service';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {

  @Input() config: LayoutConfig;
  version = environment.version;
  collapsed = false;
  unit = 'pixel';
  contentSize: string|number = '*';
  private size = 350;
  constructor(private deviceService: DeviceDetectorService, private localSettings: LocalSettingsService) { }

    get sidebarSize() {
      return this.collapsed ? 0 : this.size;
    }

    ngOnInit() {
      if (this.config.hasOwnProperty('collapsed')) {
        this.collapsed = this.config.collapsed;
      }
      const openedSidebar = this.localSettings.getSidebar(this.config.appName);
      if (typeof openedSidebar === 'boolean') {
        this.collapsed = !openedSidebar;
      }

      this.initMobile();
    }

    onToggleSidebar() {
      this.collapsed = !this.collapsed;
      this.handleToggleMobile();
      this.localSettings.setSidebar(this.config.appName, !this.collapsed);
    }

    private initMobile() {
      if (this.deviceService.isMobile()) {
        this.collapsed = true;
        this.size = 100;
        this.contentSize = 100;
        this.unit = 'percent';
      }
    }

    private handleToggleMobile() {
      if (this.deviceService.isMobile()) {
        this.contentSize = this.collapsed ? 100 : 0;
      }
    }
}
